.dp-po .dp-po-header {
  box-shadow: none;
}
.dp-po .dp-po-main,
.dp-po .dp-po-footer {
  background: none;
}
.dp-po .dp-po-title {
  margin-bottom: 0;
}
.dp-po .dp-po-block {
  padding: 0;
  box-shadow: none;
}
.dp-po .dp-po-avatar {
  display: inline-block;
}
.dp-po .dp-po-comments-extras,
.dp-po .dp-po-comments-time {
  display: inline-block;
}
.dp-po .dp-po-post-avatars-item,
.dp-po .dp-po-post-avatars-link {
  border-color: white;
}
.dp-po .dp-po-post-meta {
  padding: 0;
  margin: 0;
  background: none;
  border: none;
  font-size: 16px;
  color: #8B9293;
}
.dp-po .dp-po-post-meta dd, .dp-po .dp-po-post-meta dt {
  display: inline-block;
}
.dp-po .dp-po-post-meta dt::after {
  content: ":";
}
.dp-po .dp-po-post-meta dd {
  font-weight: bold;
}
.dp-po h3 {
  color: #8B9293;
}
.dp-po .dp-po-related-content-item {
  color: #8B9293;
}
.dp-po .dp-po-news-head {
  position: relative;
  padding-left: 18px;
}